import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Sum from '@/components/Sum'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/sum',
      name: 'Sum',
      component: Sum
    },

  ]
})



