<template>
  <div class="hello">
    <div>
      <span>共有{{wordsLength}}个单词</span>
    </div>
    <div>
      <span>记了{{countSum}}次</span>
    </div>
    <div>
      <span>已经记住{{overWordSum}}个单词</span>
    </div>
    <br>

    <div v-if='userMemoryData&&userMemoryData.daysRecord&&userMemoryData.daysRecord[todayStr]'>
      <div>
        <span>今天记了{{userMemoryData.daysRecord[todayStr].count}}个单词</span>
      </div>
      <div>
        <span>其中，pass了{{userMemoryData.daysRecord[todayStr].pass}}个</span>
      </div>
    </div>
    <div v-else>
      今天没有数据
    </div>

    <div>
      <router-link to="/home" href="javascript:void(0);">返回</router-link>
    </div>

  </div>
</template>

<script>
  import { getTodayStr, getNewWorldsData } from '../utils'

  export default {
    name: 'hello',
    data() {
      return {
        todayStr: getTodayStr(),
        overWordSum: 0,
        countSum: 0,
        wordsLength: 0,
        userMemoryData: null
      }
    },
    // async created() {
    // },
    mounted: function () {
      this.$nextTick(async () => {
        if (localStorage.userMemoryDataStr) {
          this.userMemoryData = JSON.parse(localStorage.userMemoryDataStr)
        }

        let userMemoryData = this.userMemoryData
        console.log('userMemoryData', userMemoryData)

        let overWordSum = 0
        let countSum = 0
        if (userMemoryData.daysRecord) {
          for (let day in userMemoryData.daysRecord) {
            let dayRecord = userMemoryData.daysRecord[day]
            if (dayRecord.pass) {
              overWordSum += dayRecord.pass
            }

            if (dayRecord.count) {
              countSum += dayRecord.count
            }
          }
        }

        this.overWordSum = overWordSum
        this.countSum = countSum

        let words = await getNewWorldsData('static/words.json')

        this.wordsLength = Object.keys(words).length
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>


