<template>
  <div class="hello">
    <br>
    <br>
    <br>
    <br>
    <br>

    <div class="l-ali-center">

      <span>{{en}}</span>
      <br>

      <div class="infos">
        <div v-if="isShowZh" class="infos__inner">
          <span>{{zh}}</span>
        </div>
      </div>
      <br>

      <a class="btn-little" @click="isShowZh=true" href="javascript:void(0);">show annotation</a>
      <br>
      <a class="btn-little" @click="next" href="javascript:void(0);">next</a>
      <br>
      <a class="btn-little" @click="setCurrentWordIsOver" href="javascript:void(0);">it's familiar</a>
    </div>

    <nav class="bottom-nav">
      <a to="/sum" @click='toSum' href="javascript:void(0);">统计</a>
    </nav>

  </div>
</template>

<script>
  import { getTodayStr, getNewWorldsData } from '../utils'

  //生成从minNum到maxNum的随机数
  function randomNum(minNum, maxNum) {
    switch (arguments.length) {
      case 1:
        return parseInt(Math.random() * minNum + 1, 10);
        break;
      case 2:
        return parseInt(Math.random() * (maxNum - minNum + 1) + minNum, 10);
        break;
      default:
        return 0;
        break;
    }
  }



  export default {
    name: "home",
    data() {
      return {
        en: "english",
        zh: "英语",
        isShowZh: false,
        words: {
          "accuser": {
            "zh": "控告"
          },
          "accustom": {
            "zh": "习惯"
          },
          "accustomed": {
            "zh": "惯例"
          },
          "accustomed to": {
            "zh": "惯例"
          },
          "ace": {
            "zh": "王牌"
          },
          "acetic": {
            "zh": "醋"
          },
          "ache": {
            "zh": "疼痛"
          }
        },
        wordsKeys: [],
        currentWordIndex: 0,
        todayStr: getTodayStr(),
        isOver: false,
        saveUserMemoryDataStrInterval: null

      };
    },
    methods: {
      toSum() {
        if (this.saveUserMemoryDataStrInterval) {
          clearInterval(this.saveUserMemoryDataStrInterval)
        }
        this.$router.push('/sum')
      },
      setCurrentWordIsOver() {
        if (isOver) return

        this.checkEnItemMemoryRecord(this.en)
        this.userMemoryData.memoryRecord[this.en].memoryCount += 20

        this.checkDaysRecord()
        this.userMemoryData.daysRecord[this.todayStr].pass++
        this.userMemoryData.daysRecord[this.todayStr].count++

      },
      checkDaysRecord() {
        if (!this.userMemoryData.daysRecord)
          this.userMemoryData.daysRecord = {}

        if (!this.userMemoryData.daysRecord[this.todayStr])
          this.userMemoryData.daysRecord[this.todayStr] = {}

        if (!this.userMemoryData.daysRecord[this.todayStr].count)
          this.userMemoryData.daysRecord[this.todayStr].count = 1

        if (!this.userMemoryData.daysRecord[this.todayStr].pass)
          this.userMemoryData.daysRecord[this.todayStr].pass = 1
      },
      next() {

        this.checkDaysRecord()

        if (!this.isShowZh) {
          this.userMemoryData.daysRecord[this.todayStr].pass++
        }

        console.log('today before', this.userMemoryData.daysRecord[this.todayStr].count)
        this.userMemoryData.daysRecord[this.todayStr].count++
        console.log('today +1 ', this.userMemoryData.daysRecord[this.todayStr].count)
        this.showNew()

      },

      checkEnItemMemoryRecord(en) {
        if (!this.userMemoryData.memoryRecord) this.userMemoryData.memoryRecord = {}
        if (!this.userMemoryData.memoryRecord[en]) {
          this.userMemoryData.memoryRecord[en] = {
            memoryCount: 0
          }
        }
      },
      showNew() {

        this.isShowZh = false
        let currentWordIndex = randomNum(0, this.wordsKeys.length - 1)

        if (currentWordIndex === this.currentWordIndex) {
          if (currentWordIndex > 1) {
            currentWordIndex--
          } else {
            currentWordIndex++
          }
        }

        let en = this.wordsKeys[currentWordIndex]

        this.checkEnItemMemoryRecord(en)

        if (this.userMemoryData.memoryRecord[en].memoryCount > 20) {
          return this.showNew()
        }

        this.en = en
        this.zh = this.words[this.en].zh

        this.currentWordIndex = currentWordIndex

        this.userMemoryData.memoryRecord[this.en].memoryCount++

      },

    },
    async created() {
      if (localStorage.userMemoryDataStr) {
        this.userMemoryData = JSON.parse(localStorage.userMemoryDataStr)

        console.log('this.userMemoryData', this.userMemoryData)
      } else {
        this.userMemoryData = {}
      }

      this.saveUserMemoryDataStrInterval = setInterval(() => {
        localStorage.userMemoryDataStr = JSON.stringify(this.userMemoryData)
        console.log('save userMemoryDataStr finished')
      }, 1000 * 6)

      this.words = await getNewWorldsData('static/words.json')

      this.wordsKeys = Object.keys(this.words)

      this.showNew()



      //console.log(this.todayStr)

    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .l-ali-center {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .infos {
    min-height: 30px;
  }

  .btn-little {
    display: block;
    border: solid #333 1px;
    border-radius: 3px;
    color: black;
    text-decoration: none;
    padding: 3px 7px;
    width: 150px;
    text-align: center;
  }

  .bottom-nav {
    position: fixed;
    bottom: 15px;
  }
</style>


